import './Login.scss';
import React from 'react';
import { useNavigate } from 'react-router-dom';
import { useCookies } from 'react-cookie';
import { authService } from './../../services/auth.service';

const Login = (props) => {
    const [cookies, setCookie, removeCookie] = useCookies(['token']);

    const navigate = useNavigate();
    const inputUser = React.createRef();
    const inputPass = React.createRef();
    

    const loginFn = e => {
        e.preventDefault();

        const login = inputUser.current.value;
        const password = inputPass.current.value;

        const data = { login, password };

        authService.login(data)
        .then((res) => {
            if (res.data.success) {
                setCookie('token', res.data.token, { path: '/', maxAge: 3600 }); 
                navigate('/home');
            }
        });
    };

    return (
        <div className="App-Login">
            <div className="everything">
                <h1>Login Form</h1>
                <div className="inputs">
                    <input ref={inputUser} className="input-box" placeholder="user"></input>
                    <input ref={inputPass} className="input-box" type="password" placeholder="password"></input>
                </div>
                <button onClick={e => loginFn(e)} className="btn--login">Zaloguj się</button>
            </div>
        </div>
    )
}

export default Login;