import './Plans.scss';
import React from 'react';
import Header from './../Header';

const Plans = (props) => {

    const hours = [
        ["08:55", "09:40", 10],
        ["09:50", "10:35", 15],
        ["10:50", "11:35", 10],
        ["11:45", "12:30", 30],
        ["13:00", "13:45",  15],
        ["14:00", "14:45", 10],
        ["14:55", "15:40", 5],
        ["15:45", "16:30", 0]
    ]

    const przedmioty = [
        'Język Angielski',
        'Informatyka'
    ]

    return (
        <div className="App-Plans">
            <Header></Header>
            <h1>Edytuj plan zajęć</h1>
            <div className="table-center">
                <div className="table">
                    {['Poniedziałek', 'Wtorek', 'Środa', 'Czwartek', 'Piątek'].map(val => {
                        return (
                            <div className="column">
                                <p>{val}</p>
                                <div className="sub">
                                    {hours.map((hour, index) => {
                                        return (
                                            <div className="selection">
                                                <span>{index + 1}. {hour[0]} - {hour[1]}</span>
                                                <select>
                                                    <option>Przedmiot</option>
                                                    {przedmioty.map((przedmiot, index) => {
                                                        return <option>{przedmiot}</option>
                                                    })}
                                                </select>
                                            </div>
                                        )
                                    })}
                                </div>
                            </div>
                        )
                    })}
                </div>
            </div>
        </div>
    )
}

export default Plans;